<script>
import { DropdownMenu, DropdownItem, NavBar, Dialog } from 'vant';
import Vue from 'vue';
export default {
  name: "index",
  components: {
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem,
    [NavBar.name]: NavBar,
  },
  data () {
    return {
      account: this.$account,
      addOpt: [
      ],
      msg: {
        formAddress: '0xb7bc89ba2e49f52782da4f926be5a7a7ac1f1a94',
