      },
      showFormAddress: '',
      qrCode: {
        packetId: null,
        key: null,
        sign: null
      },
      contract: {
        account: null,
        instance: null
      }
    }
  },
  created () {
    this.getWalletAddress();
  },
  mounted () {
  },
  methods: {
    // 获取钱包地址
    getWalletAddress () {
      // tp.getCurrentWallet().then((res) => {
      //   console.log(res.data.blockchain)
      //   if (res.data.blockchain != "eth") {
      //               // console.log("res.data   2323")
      //     Dialog.confirm({
